.rs-panel {
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid #DB2228 !important;
  margin-bottom: 20px;
}

.rs-panel-bordered {
  border: 1px solid #e5e5ea;
  border: 1px solid var(--rs-border-primary);
}

.rs-panel-shaded {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.12), 0 0 10px rgba(0, 0, 0, 0.06);
  box-shadow: var(--rs-panel-shadow);
}

.rs-panel-header,
.rs-panel-body {
  padding: 20px;
}

.rs-panel-body-fill {
  padding: 0 !important;
}

.rs-panel-header {
  color: #272c36;
  color: var(--rs-text-heading);
  font-size: 16px;
  line-height: 1.25;
}

.rs-panel-title {
  margin: 0;
}

.rs-panel-title a {
  color: inherit;
}

.rs-panel-title a:hover,
.rs-panel-title a:focus,
.rs-panel-title a:active {
  text-decoration: none;
}

.rs-panel-header + .rs-panel-collapse .rs-panel-body,
.rs-panel-header + .rs-panel-body {
  padding-top: 0;
}

.rs-panel-collapsible > .rs-panel-header {
  cursor: pointer;
  transition: background-color 0.3s linear, border-radius 0.3s linear;
  position: relative;
}

.rs-panel-collapsible > .rs-panel-header > .rs-icon {
  color: #8e8e93;
  color: var(--rs-text-secondary);
  position: absolute;
  top: 24px;
  right: 20px;
  transition: -webkit-transform 0.3s linear;
  transition: transform 0.3s linear;
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}

.rs-panel-group {
  border-radius: 6px;
  overflow: hidden;
}

.rs-panel-group-bordered {
  border: 1px solid #e5e5ea;
  border: 1px solid var(--rs-border-primary);
}

.rs-panel-group > .rs-panel {
  border: none;
}

.rs-panel-group > .rs-panel + .rs-panel {
  position: relative;
}

.rs-panel-group > .rs-panel + .rs-panel::before {
  content: '';
  position: absolute;
  top: 0;
  border-top: 1px solid #e5e5ea;
  border-top: 1px solid var(--rs-border-primary);
  left: 20px;
  right: 20px;
}

.customer-detail .rs-panel-group-bordered{
  border:none !important;
}

.customer-detail .rs-panel-header, .rs-panel-body{
  padding: 10px !important;
}

